<script>
import Hello from "./components/Hello.vue";
import Loader from "./components/Loader.vue";
import Game from "./components/Game.vue";

export default {
  name: "App",
  components: {
    Hello,
    Loader,
    Game,
  },
  data() {
    return {
      isHelloMessageShown: false,
      isGameStart: false,
      isMobile: false,
    };
  },
  mounted: function() {
    if (window.innerWidth < 991) {
      this.isMobile = true;
    }
    setTimeout(() => {
      this.isHelloMessageShown = true;
    }, 2500);
  },
  methods: {
    onStartGame() {
      this.isGameStart = true;
      this.$refs.audio.play();
    },
    mute() {
      this.$refs.audio.pause();
    },
  },
};
</script>

